.card {
  width: 300px;
  background: rgb(31, 31, 31);
  border: 1px solid rgb(255, 255, 255);
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin-bottom: 50px;
  /* padding: 10px; */
}

.img {
  margin: 0 auto;
  width: 250px;
  height: 250px;
  padding: 10px;
  margin-bottom: 10px;
  margin-top: 5px;
}

.name {
  font-size: 27px;
  font-weight: 700;
  margin-left: 10px;
  margin-bottom: 7px;
}
.description {
  font-size: 22px;
  font-weight: 500;
  margin-left: 10px;
  margin-bottom: 10px;
}
