.about {
  display: flex;
  flex-direction: row;
  background: rgb(0, 0, 0);
  color: #fff;
}

.head {
  font-size: 52px;
  margin-bottom: 15px;
  display: flex;
  justify-content: center;
  margin-bottom: 25px;
}

.image {
  width: 40%;
  height: 400px;
  margin-left: 25px;
  margin-right: 35px;
  border: 5px solid #fff;
  display: flex;
  align-self: flex-start;
}

p {
  font-size: 32px;
  display: flex;
  align-self: flex-end;
  margin-right: 50px;
  margin-bottom: 50px;
}
